export const MUTATE_AUTH_USER = (state, authUser) => {
  state.authUser = authUser
}

export const MUTATE_JWT_TOKEN = (state, jwtToken) => {
  state.jwtToken = jwtToken
}

export const MUTATE_AUTH_OVERTIME = (state, overtime) => {
  state.authUser.overtime = overtime
}



