<template>
    <v-app>
        <v-content>
            <v-container>
                <router-view ref="routerView"></router-view>
            </v-container>
        </v-content>
        <v-footer app fixed style="background: rgba(0, 0, 0, 0.7)">
            <div class="grey--text">(C) 2017 PIN sp. z o. o.</div>
            <v-spacer></v-spacer>
            <div class="grey--text"><small>Credits: <span href='http://dryicons.com/icon-packs/aesthetica'>Dryicons</span>, <span href="https://thenounproject.com/">Garrett Knoll (Noun Project)</span></small></div>
        </v-footer>
    </v-app>
</template>

<script>
export default {
  methods: {
    dragOver (event) {
      event.stopPropagation()
      event.preventDefault()
      event.dataTransfer.dropEffect = 'move'
    },

    dropFile (event) {
      event.stopPropagation()
      event.preventDefault()
      this.$refs.routerView.addFiles(event.dataTransfer.files)
    },
  },

  async mounted () {
    const statusName = this.$route.params.statusName
    this.$vuetify.theme.primary = statusName === 'graphicsfile' ? '#1976D2' : '#8e24aa'
    this.$el.classList.add(`status-${statusName}`)

    this.$el.addEventListener('dragover', this.dragOver, false)
    this.$el.addEventListener('drop', this.dropFile, false)
  },

  beforeDestroy () {
    this.$el.removeEventListener('dragover', this.dragOver)
    this.$el.removeEventListener('drop', this.dropFile)
  },
}
</script>

<style lang="css">
    .application.status-graphicsfile {
        background: url("../static/images/app-background-graphicsfile-800.jpg") 0 0 no-repeat;
        background-size: cover;
    }

    @media screen and (min-width: 600px) {
        .application.status-graphicsfile {
            background-image: url("../static/images/app-background-graphicsfile-1600.jpg");
        }
    }

    .application.status-masterfile {
        background: url("../static/images/app-background-masterfile-800.jpg") 0 0 no-repeat;
        background-size: cover;
    }

    @media screen and (min-width: 600px) {
        .application.status-masterfile {
            background-image: url("../static/images/app-background-masterfile-1600.jpg");
        }
    }

    main {
        background: rgba(0, 0, 0, 0.8)
    }
</style>



